/*Contenedor principal*/
.contenedor-principa--log {
  background-color: var(--primario);
  font-family: "Lato", sans-serif;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0; }

.video {
  position: relative;
  max-height: 50rem;
  overflow: hidden; }

.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#0024a8+0,7db9e8+90,7db9e8+90&0.79+0,0+99 */
  background: -moz-linear-gradient(top, rgba(0, 36, 168, 0.79) 0%, rgba(125, 185, 232, 0.07) 90%, rgba(125, 185, 232, 0) 99%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(0, 36, 168, 0.79) 0%, rgba(125, 185, 232, 0.07) 90%, rgba(125, 185, 232, 0) 99%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(0, 36, 168, 0.79) 0%, rgba(125, 185, 232, 0.07) 90%, rgba(125, 185, 232, 0) 99%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#c90024a8', endColorstr='#007db9e8',GradientType=0 );
  /* IE6-9 */ }

.video video {
  display: block;
  width: 100%;
  margin-top: -20rem; }

.contenido-video {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1; }

/*Pantalla inicial*/
.principal {
  text-align: center;
  color: #fff;
  width: 100%;
  margin-top: 30rem;
  height: 50rem; }

.img {
  width: 15%; }

/* Full-width input fields */
.login-text {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box; }

/* Set a style for all buttons */
.titulo--log {
  width: 100%; }

.titulo--log button {
  background-color: #ff8800;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 20%; }

.boton-acceder {
  background-color: #ff8800;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
  border-radius: 8px;
  font-size: 2rem; }

.boton-acceder:hover {
  opacity: 0.8; }

/* Extra styles for the cancel button */
.cancelbtn {
  width: auto;
  padding: 10px 18px;
  background-color: #f44336; }

/* Center the image and position the close button */
.imgcontainer {
  text-align: center;
  margin: 24px 0 12px 0;
  position: relative; }

img.avatar {
  width: 10%;
  border-radius: 50%; }

.container {
  padding: 16px; }

span.psw {
  float: right;
  padding-top: 16px; }

/* The Modal (background) */
.modal {
  /* Hidden by default */
  display: none;
  position: fixed;
  /* Stay in place */
  z-index: 1;
  /* Sit on top */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  /*height: 100%;  Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: black;
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */
  padding-top: 60px; }

/* Modal Content/Box */
.modal-content {
  background-color: #b4b4b4;
  margin: 2% auto 15% auto;
  /* 5% from the top, 15% from the bottom and centered */
  width: 40%;
  /* Could be more or less, depending on screen size */
  height: 80%; }

/* The Close Button (x) */
.close {
  position: absolute;
  right: 25px;
  top: 0;
  color: #000;
  font-size: 35px;
  font-weight: bold; }

.close:hover,
.close:focus {
  color: red;
  cursor: pointer; }

/* Add Zoom Animation */
.animate {
  -webkit-animation: animatezoom 0.6s;
  animation: animatezoom 0.6s; }

@-webkit-keyframes animatezoom {
  from {
    -webkit-transform: scale(0); }
  to {
    -webkit-transform: scale(1); } }

@keyframes animatezoom {
  from {
    transform: scale(0); }
  to {
    transform: scale(1); } }

/*FOTERR*/
.footer {
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 15.3rem;
  background-color: var(--secundario); }

/* Change styles for span and cancel button on extra small screens */
